nav.navbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-self: center;  
  letter-spacing: 0.05rem;
  background: #234465;
  padding: 0.8rem 1rem;
  color: rgba(237, 244, 253, 0.966);
}
#site-header > nav > section.navbar-dashboard > a{
    list-style: none;
    text-decoration: none;
    align-self: center;
    font-weight: bold;
    font-size: 1.5rem;
    color: white;
    
}
#site-header > nav > section.navbar-dashboard > form > input[type="text"],
#site-header > nav > section.navbar-anonymous > ul > li:nth-child(1) > a,
#site-header > nav > section.navbar-user > ul > li:nth-child(1),
#site-header > nav > section.navbar-dashboard > ul > li:nth-child(1) > a{
    margin-right: 0.5rem;

}
nav.navbar ul {
  background: transparent;
  padding: 0.5rem;
  border-radius: 0.3rem;
  margin: 0;
}
nav.navbar ul li {
  display: block;
  list-style: none outside;
  position: relative;
}
nav.navbar ul li a {
  display: block;
  padding: 0.4rem 0.8rem;
  border-radius: 0.4rem;
  text-decoration: none;
  font-weight: bold;
  background: #234465;
  color: rgba(237, 244, 253, 0.966);
}
.button:hover,
nav.navbar ul li a:hover,
nav.navbar ul li:hover > a {
  background: rgb(248, 215, 107);
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.dashboard nav{
    background: transparent;
}
.navbar-dashboard{
    display: flex;
    flex-direction: column;
    justify-content: space-between;    
    width: 95vw;
}
.navbar .first-bar{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.navbar .first-bar a{
    /* color: white; */
    margin-right: 0.5rem;
}
.navbar .first-bar input{  
    margin-right: 0.5rem;
}
#site-header > nav > section > div.first-bar > a:nth-child(1){
    font-weight: bold;
    font-size: 1.5rem;
}
.navbar .first-bar form{
    display: flex;
    align-items: center;
}
.second-bar {
    float: right;
}
.navbar .second-bar ul{
    display: flex;
    align-items: center
}
.navbar .second-bar ul li{
    margin-right: 0.5rem;
}
.navbar-anonymous ul{
    display: flex;
}

.nav-button {
    border: 2px solid white;
    background: transparent;
}

.nav-first-line {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.nav-second-line {
    display: flex;
    justify-content: center;
}

.fa-bars {
    display: none;
}

@media only screen and (max-width: 600px) {
    .navbar .first-bar{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .navbar .first-bar .button {
        display: none;
    }

    .fa-bars {
        color: white !important;
        display: flex;
        margin-top: 170%
    }
  }