@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,400i,700,700i);
@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700);
@import url(https://fonts.googleapis.com/css?family=PT+Sans|PT+Serif+Caption:400i|PT+Serif:400,400i,700,700i);
* {
	box-sizing: border-box;
   }

*:before,
*:after {
 box-sizing: border-box;
}

html {
	font: 12px/1.5 "PT Sans", "PT Serif Caption", "PT Serif";	
	height: 100%;
	position: relative;
}

@media screen and (min-width: 500px) {
	html { 
		font-size: 14px;
	 }
}

@media screen and (min-width: 700px) {
	html { 
		font-size: 16px;
	 }
}

@media screen and (min-width: 1600px) {
	html { 
		font-size: 18px;
	 }
}

body {
    padding: 0;
    margin: 0;
	background-color: #eff4f4;
	background-image: url(/static/media/texture-pattern-light-background-gradient-light-texture_1920x1200_sc.880d866a.jpg);
	background-size: cover;
	height: 100%;
	position: relative;
}

body, table, th, td, form, input, textarea, option, select {
	font: inherit;
}

h1, h2, h3, h4, h5, h6 {
	line-height: 1.2;	
}

h1 { font-size: 2.2rem; }
h2 { font-size: 2.0rem; }
h3 { font-size: 1.8rem; }
h4 { font-size: 1.6rem; }
h5 { font-size: 1.4rem; }
h6 { font-size: 1.2rem; }

ul, ol {
	padding-bottom: 1.5em;
}

ul, ol {
	padding-left: 2em;
}

li {
	list-style-position: inside;
    list-style-type: none;
}

blockquote {
	width: 80%;
	margin: 0 auto;
	border-left: 4px solid #DDD;
	font-style: italic;	
	padding: 1rem 2rem;
	position: relative;
}

#root {
	min-height: 100vh; /* will cover the 100% of viewport */
	overflow: hidden;
	display: block;
	position: relative;
}
nav.navbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-self: center;  
  letter-spacing: 0.05rem;
  background: #234465;
  padding: 0.8rem 1rem;
  color: rgba(237, 244, 253, 0.966);
}
#site-header > nav > section.navbar-dashboard > a{
    list-style: none;
    text-decoration: none;
    align-self: center;
    font-weight: bold;
    font-size: 1.5rem;
    color: white;
    
}
#site-header > nav > section.navbar-dashboard > form > input[type="text"],
#site-header > nav > section.navbar-anonymous > ul > li:nth-child(1) > a,
#site-header > nav > section.navbar-user > ul > li:nth-child(1),
#site-header > nav > section.navbar-dashboard > ul > li:nth-child(1) > a{
    margin-right: 0.5rem;

}
nav.navbar ul {
  background: transparent;
  padding: 0.5rem;
  border-radius: 0.3rem;
  margin: 0;
}
nav.navbar ul li {
  display: block;
  list-style: none outside;
  position: relative;
}
nav.navbar ul li a {
  display: block;
  padding: 0.4rem 0.8rem;
  border-radius: 0.4rem;
  text-decoration: none;
  font-weight: bold;
  background: #234465;
  color: rgba(237, 244, 253, 0.966);
}
.button:hover,
nav.navbar ul li a:hover,
nav.navbar ul li:hover > a {
  background: rgb(248, 215, 107);
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.dashboard nav{
    background: transparent;
}
.navbar-dashboard{
    display: flex;
    flex-direction: column;
    justify-content: space-between;    
    width: 95vw;
}
.navbar .first-bar{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.navbar .first-bar a{
    /* color: white; */
    margin-right: 0.5rem;
}
.navbar .first-bar input{  
    margin-right: 0.5rem;
}
#site-header > nav > section > div.first-bar > a:nth-child(1){
    font-weight: bold;
    font-size: 1.5rem;
}
.navbar .first-bar form{
    display: flex;
    align-items: center;
}
.second-bar {
    float: right;
}
.navbar .second-bar ul{
    display: flex;
    align-items: center
}
.navbar .second-bar ul li{
    margin-right: 0.5rem;
}
.navbar-anonymous ul{
    display: flex;
}

.nav-button {
    border: 2px solid white;
    background: transparent;
}

.nav-first-line {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.nav-second-line {
    display: flex;
    justify-content: center;
}

.fa-bars {
    display: none;
}

@media only screen and (max-width: 600px) {
    .navbar .first-bar{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .navbar .first-bar .button {
        display: none;
    }

    .fa-bars {
        color: white !important;
        display: flex;
        margin-top: 170%
    }
  }
